import React from 'react';
import { Row } from 'react-bootstrap';

import PortfolioGenerator from '../generator/PortfolioGenerator';
import Autograder from './Autograder';
import Sportify from './Sportify';
import ArtCollection from './ArtCollection';

// https://gorangajic.github.io/react-icons/fa.html


function getModalInfo() {
    return {
        Autograder: {
            modalName: "Autograder",
            id: "auto",
            logo: "/files/autograder/autograder.png",
            gallery: ['/files/autograder/LabCheckoff.png', '/files/autograder/TutorQueueStats1.png',
                '/files/autograder/TutorQueueStats2.png', '/files/autograder/WelcomePage.png',
                '/files/autograder/TutorQueue.png'],
            getBody: () => <Autograder/>
        },
        Sportify: {
            modalName: "Sportify",
            id: "sport",
            logo: "/files/sportify/sportify.png",
            gallery: ["/files/sportify/activityList.jpg", "/files/sportify/createActivity.jpg",
                      "/files/sportify/login.jpg", "/files/sportify/signup.jpg"],
            getBody: () => <Sportify/>
        },
        ArtCollection: {
            modalName: "Art Collection",
            id: "ArtCollection",
            logo: "/files/artCollection/artcollectionlogo.jpg",
            getBody: () => <ArtCollection/>
        }
    };
}


export default function Projects () {
    return (
        <div>
            <div className="header">
                <h2 className="title">Projects</h2>
            </div>
            <Row>
                <div>
                    <PortfolioGenerator modalInfo={ getModalInfo() }/>
                </div>
            </Row>
        </div>

    );
}



